import { Component } from "react";
import 'leaflet/dist/leaflet.css'
import L, { LatLng, LatLngBounds } from 'leaflet'
import mapSVG from './../assets/map.svg'
import iconUrl from 'leaflet/dist/images/marker-icon.png'
import iconRetinaUrl from 'leaflet/dist/images/marker-icon-2x.png'
import shadowUrl from 'leaflet/dist/images/marker-shadow.png'


export default class Map extends Component {

    constructor(props) {
        super(props)
        this.mapObject = ''
    }

    componentDidMount() {
        delete L.Icon.Default.prototype._getIconUrl;
        L.Icon.Default.mergeOptions({
            iconRetinaUrl: iconRetinaUrl,
            iconUrl: iconUrl,
            shadowUrl: shadowUrl
        });

        this.mapObject = L.map('mapWrapper', {
            center: new LatLng(
                75.29773546875684,
                74.66308593750001
            ),
            zoom: 6,
            maxBounds: new LatLngBounds({
                lat: 78.78489872650539,
                lng: 51.02050781250001
            }, {
                lat: 73.37821477939463,
                lng: 99.49218750000001
            }),
            minZoom: 6,
            maxZoom: 8
        });

        // helper event to locate coordinates and other metadata
        this.mapObject.on("click", (e) => {
            console.log(e)
        })


        L.imageOverlay(mapSVG, [[79.35958957209913, 50.00976562500001], [72.39570570653261, 100.09765625000001]]).addTo(this.mapObject)
        
        // storing map into the parent's state
        this.props.getMapObject(this.mapObject)
    }

    showMarker() {

    }
    
    render() {
        return (
            <div className="map w-full h-full" id="mapWrapper">
                <div id="swiper-amenities fixed top-0 left-0">
                    {this.props.test}
                </div>
            </div>
        )
    }
}