import { createRef, useRef, useState } from "react"
import { Link } from "react-router-dom"

const Card = (props) => {

    const [cardContent, setCardContent] = useState(props.content)

    const firstTabRef = createRef()

    let tabs = ''

    if ( props.tabs ) {
        tabs = Object.keys(props.tabs).map((index) => {
    
            if ( index == 0) {
                return (
                    <span key={`tab_${index}${Date.now()}`} ref={firstTabRef} className="bg-white py-2 px-4 rounded-2xl shadow-md uppercase tracking-wider font-medium hover:bg-indigo-600 hover:text-white transition-all cursor-pointer" onClick={() => { setCardContent(props.tabs[index].content) }}>{ props.tabs[index].title }</span>
                )
            }
            return (
                <span key={`tab_${index}${Date.now()}`} className="bg-white py-2 px-4 rounded-2xl shadow-md uppercase tracking-wider font-medium hover:bg-indigo-600 hover:text-white transition-all cursor-pointer" onClick={() => { setCardContent(props.tabs[index].content) }}>{ props.tabs[index].title }</span>
            )
        })
    }

    return (
        <section className="rounded-2xl bg-white overflow-hidden shadow-lg">
            <div className="card-header bg-gray-100 px-4 py-2 flex justify-between items-center">
                <h2>{props.title}</h2>
                { props.closeAction ? (
                    <span className="font-bold text-base" onClick={props.closeAction}>x</span>
                ) : (
                    ''
                ) }
            </div>
            <div className="card-body border-t-2 border-b-2 max-h-96 overflow-y-auto">
                
                {cardContent}
            </div>
            <div className="card-footer bg-gray-100 p-4 flex justify-evenly">
                {tabs}
            </div>
        </section>
    )
}

export default Card