import { Component, createRef } from "react"
import L, { LatLng, LatLngBounds } from 'leaflet'

import Card from "../components/Card"
import Map from "../components/Map"


import washroomAm from './../assets/images/washroomAm.svg';
import playgroundAm from './../assets/images/playgroundAm.svg';
import AmenitiesCard from "../components/AmenitiesCard";
import Gallery from "../components/Gallery";

import img from './../assets/images/marsh/img.jpg'
import img2 from './../assets/images/marsh/img2.jpg'
import img3 from './../assets/images/marsh/img3.jpg'
import img4 from './../assets/images/marsh/img4.jpg'
import img5 from './../assets/images/marsh/img5.jpg'
import img6 from './../assets/images/marsh/img6.jpg'
import img7 from './../assets/images/marsh/img7.jpg'
import img8 from './../assets/images/marsh/img8.jpg'

export default class Main extends Component {

    constructor(props) {
        super(props)

        this.state = {
            isPointOfInterestSelected: false,
            pointOfInterest: {
                title: '',
                subtitle: '',
                amenities: '',
                content: '',
                coordinates: {
                    lat: 0,
                    lng: 0
                }
            },
            pointOfInterestMenu: false, // true = open, false = close
            testState: "This is a Test",
            mapObject: false
        }
    }

    focusOn(point) {

        if ( this.state.mapObject ) {
            this.state.mapObject.setView({
                lat: point.coordinates.lat,
                lng: point.coordinates.lng
            }, point.coordinates.zoom, {
                "animate": true,
                "pan": {
                    "duration": 1
                }
            })

            let youAreHere = L.popup({
                autoClose: false,
                className: "popup-theme",
                closeButton: false
            }).setLatLng({
                lat: point.coordinates.lat,
                lng: point.coordinates.lng
            }).setContent(`
            <h1 class="font-bold uppercase tracking-widest">You Are Here</h1>
            `).openOn(this.state.mapObject)
        }

        this.setState({
            isPointOfInterestSelected: true,
            pointOfInterest: {
                title: point.title,
                content: point.description,
                coordinates: {
                    lat: point.coordinates.lat,
                    lng: point.coordinates.lng,
                }
            }
        })
    }

    togglePointOfInterestMenu() {
        this.setState({
            pointOfInterestMenu: ( this.state.pointOfInterestMenu ) ? false : true
        })
    }

    getMapObject(obj) {
        this.setState({
            mapObject: obj
        })
    }

    render () {

        let pointsOfInterest = Object.keys(this.props.points).map((index) => {
            let point = this.props.points[index]
            return (
                <div key={`poi_${index}`} className="bg-white p-4 shadow-md rounded-xl cursor-pointer" onClick={() => { this.focusOn(point) }} >{point.title}</div>
            )
        })

        let tabs = [
            {
                title: "Info",
                content: ( 
                    <div className="p-4">
                        <h3 className="text-lg font-medium uppercase tracking-widest mb-2">About</h3>
                        <div className="flex">
                            <Gallery images={[img, img2, img3, img4, img5, img6, img7, img8]} variant="flex" />
                        </div>
                        {this.state.pointOfInterest.content}
                    </div>
                    )
            }, {
                title: "Amenities",
                content: (
                    <div className="flex-wrap bg-white p-5 card-wrapper">
                        <AmenitiesCard title="Washroom" subtitle="8 points" thumbnail={washroomAm} />
                        <AmenitiesCard title="Playground" subtitle="8 points" thumbnail={playgroundAm} />
                        <AmenitiesCard title="Amenity3" subtitle="8 points" thumbnail={playgroundAm}  />
                        <AmenitiesCard title="Amenity4" subtitle="8 points" thumbnail={playgroundAm}  />
                        <AmenitiesCard title="Amenity5" subtitle="8 points" thumbnail={playgroundAm} />
                        <AmenitiesCard title="Amenity6" subtitle="8 points" thumbnail={playgroundAm} />
                        <AmenitiesCard title="Amenity7" subtitle="8 points" thumbnail={playgroundAm} />
                    </div>
                )
            }, {
                title: "Explore",
                content: "Things To Do Here"
            }
        ]

        return (
            <>
                <div className="map-wrapper fixed top-0 left-0 w-full h-full m-0 p-0 z-0">
                    <Map getMapObject={(obj) => {this.getMapObject(obj)}} />
                </div>
                <div className={`fixed bottom-0 left-0 w-full max-h-full p-5 md:bottom-auto md:max-w-md md:left-auto md:right-0 md:top-0 component ${this.state.isPointOfInterestSelected ? '' : 'component-hidden'}`}>
                    <Card title={this.state.pointOfInterest.title} tabs={tabs} closeAction={() => {this.setState({isPointOfInterestSelected: false, pointOfInterestMenu: false})}} />
                </div>
                <div className={`fixed bottom-0 left-0 w-full max-h-full p-5 md:bottom-auto md:max-w-md md:left-auto md:right-0 md:top-0 component ${(!this.state.isPointOfInterestSelected && !this.state.pointOfInterestMenu) ? '' : 'component-hidden'}`}>
                    <div className="flex flex-row bg-white shadow-lg rounded-2xl transition uppercase tracking-widest text-gray-600 items-end">
                        <a href="#" className="block w-full p-5" onClick={() => { this.togglePointOfInterestMenu() }}>List of Points</a>
                    </div>
                </div>
                <div className={`fixed bottom-0 left-0 w-full max-h-full p-5 md:bottom-auto md:max-w-md md:left-auto md:right-0 md:top-0 component ${(!this.state.isPointOfInterestSelected && this.state.pointOfInterestMenu) ? '' : 'component-hidden'}`}>
                    <div className="grid grid-cols-1 gap-4 rounded-xl overflow-hidden bg-gray-100 p-4">
                        { pointsOfInterest }
                    </div>
                </div>

            </>
        )
    }
}